.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}



.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* /네비게이션 */

#navbar-nav {
 background-color:#4b8106;
}

#whole {
  width: 100%;
  height: 100%;
  background-color:#4b8106; }

#hide {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

.top_mess {
  color:#fff;
  font-size:14.5px;
}
.top_mess:nth-child(3) {
  display: flex;
}

.top_mess #navbarScrollingDropdown {
  color:#fff;
}

/*메인네비게이션*/

#search {
  width:26px;
  margin:0 18px 0 0px;
  cursor: pointer;
}


#person {
  width:28px;
  margin:0 18px 0 0px;
  cursor: pointer;
}


#logo {
 width:100px
}

#item {
  background-color:#4b8106;
  border:none;
  font-size:14px;
  padding: 5px 18px;
}


/* 슬라이더 */

.slidercontents {
  height: 480px;
  background-image: url(../public/img/slider.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
}

.slidercontents2 {
  height: 480px;
  background-image: url(../public/img/slider2.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
}

.wrapText {
  margin-left: 12%;
}

.slidercontents h1, .slidercontents2 h1 {
  width:45%;
  font-weight: 700;
  letter-spacing: 0;
  font-size: 6.2vmin;
}

.slidercontents p, .slidercontents2 p {
  color:#666;
  font-size:15.5px;
  width: 70%;
}

.slidercontents button, .slidercontents2 button {
  border:none;
  border-radius: 8px;
  padding: 10px 33px;
  color:#fff;
  font-size: 15px;
  background-color:#4b8106;
  margin-top: 3%;
}

.slidercontents button:hover {
  background-color:#121212;
  transition: all 0.8s;
}

.slidercontents2 button:hover {
  background-color:#121212;
  transition: all 0.8s;
}

.buttons {
  padding: 11px 33px;
  margin-right: 10px;
  font-weight: 600;
  border-radius: 11px;
}


.buttons:hover {
  background-color:#4b8106;
  color:#fff;
}

/* 탑 카테고리 */

#topCategory {
  margin-top: 50px;
  margin-bottom: 30px;
  text-align: center;
  font-weight: 700;
}

#productImg {
  border-radius: 100%;
  cursor: pointer;
  width: 85%;
}

#productTitle {
  text-align: center;
  margin-top: 15px;
  cursor: pointer;
}

#productTitle:hover {
  color:#4b8106;
  transition: all 0.5s;
}

/* 트렌딩 프로덕트 */

#trending {
  text-align: center;
  margin: 10px 0 30px 0px;

}

#trendingPro {
  margin-top: 75px;
  margin-bottom: 30px;
  text-align: center;
  font-weight: 700;
}

#trendingBox {
  border: 1px solid #ddd;
  border-radius: 10px;
  height: 100%;
  overflow: hidden;
  padding: 3% 0px;
}


#mainImg {
  width:100%;
  height: 78%;
}

#mainImg > img:hover {
  opacity: 0.7;
  transition: all 0.6s;
}


#mainText {
  height: 22%;
  overflow: hidden;
}

#mainText > p:first-of-type {
  color:#777;
  margin: 0 0 0 4%;
  padding: 0;
  font-weight: 300;
  font-size: 13px;
}

#mainText > p:last-of-type {
  margin: 0 0 0 4%;
  font-size:15px
}

#mainText > h5 {
  margin: 0 0 0 4%;
}

.na {
  color:#333;
  height: 90%;
  margin: 0;
  padding: 0;
}

.na:hover {
  color:#4b8106
}

.mainBtn {
  border:none;
  background-color: #4b8106;
  color:#fff;
  margin: 0 0 0 3%;
  width: 94%;
  line-height: 85%;
  border-radius: 7px;
  font-size: 14px;
  padding: 4% 12%;
}

.mainBtn:hover {
  background-color: #121212;
  transition: all 0.5s;
}

/* 정보 박스 4개 */

#inforImg {
  width:80px;
  height: 80px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ecf2e6;
  margin: 0 auto;
}

#inforTitle {
  font-size: 18px;
