/* 블로그 */

#blogTitle {
  margin-top: 50px;
  margin-bottom: 30px;
  text-align: center;
  font-weight: 700;
}

#blogBox {
  border: 1px solid #ddd;
  height: 100%;
  border-radius: 15px;
  box-shadow: 1px 1px 3px #ddd;
  overflow: hidden;
}

#blogImg {
  width: 100%;
  height: 60%;
  overflow: hidden;
  cursor: pointer;
}

#blogImg > img {
  width:100%;
  height: 100%;
}


#blogImg>img:hover {
  transform: scale(1.1);
  transition: all 0.6s;
}

#blogText {
  padding: 1% 3%;
  height: 37%;
}

#blogText > p:first-of-type, p:last-of-type {
  color: #777;
  font-size: 14px;
  margin: 0;
  padding: 0;
}

#blogText > p:nth-of-type(2) {
  font-size: 20px;
  font-weight: 500;
  margin: 0;
  padding: 0;
}


